.img-figure {
  position: absolute;
  margin: 0;
  padding: 14px;
  background: #F4F0EC;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 6px double #483C32;
  cursor: pointer;
  -webkit-transform-origin: 0 50% 0;
          transform-origin: 0 50% 0;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  transition: left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, left 0.6s ease-in-out, top 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}
.img-figure .fig-caption {
    text-align: center;
}
.img-figure .fig-caption .img-title {
      margin: 10px 0 0 0;
      line-height: 30px;
      color: #a7a0a2;
      font-size: 16px;
      background-color: antiquewhite;
}
.img-figure .fig-caption .img-back {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 50px 40px;
      overflow: auto;
      color: #a7a0a2;
      font-size: 22px;
      line-height: 1.25;
      text-align: left;
      background-color: #F4F0EC;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      -webkit-transform: rotateY(-180deg);
              transform: rotateY(-180deg);
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
}
.img-figure .fig-caption .img-back.img-inverse {
        -webkit-backface-visibility: visible;
                backface-visibility: visible;
}
.img-figure .fig-caption .img-back p {
        margin: 0;
}

