.controller-unit {
  display: inline-block;
  margin: 0 5px;
  width: 30px;
  height: 30px;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  background-color: #aaa;
  border-radius: 50%;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
  transition: background-color 0.3s, -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, background-color 0.3s;
  transition: transform 0.6s ease-in-out, background-color 0.3s, -webkit-transform 0.6s ease-in-out;
}
.controller-unit.is-center {
    background-color: #888;
    -webkit-transform: scale(1);
            transform: scale(1);
}
.controller-unit.is-center::after {
      color: #fff;
      font-family: 'icons-turn-arrow';
      font-size: 80%;
      line-height: 30px;
      content: '\E600';
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
}
.controller-unit.is-center.is-inverse {
      background-color: #555;
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
}
